.effect-18{border: 0; padding: 4px 0; border-bottom: 1px solid #a6a7a9; background-color: transparent;
  font-family: 'Open Sans',Arial,Helvetica,sans-serif!important;
}
.effect-18 ~ .focus-border{position: absolute; bottom: 0; left: 0; width: 100%; height: 2px; z-index: 99;}
.effect-18 ~ .focus-border:before, 
.effect-18 ~ .focus-border:after{content: ""; position: absolute; bottom: 0; left: 0; width: 0; height: 100%; background-color: black; -webkit-transition: 0.4s; transition: 0.4s;}
.effect-18 ~ .focus-border:after{left: auto; right: 0;}
.effect-18:focus ~ .focus-border:before, 
.effect-18:focus ~ .focus-border:after,
.has-content.effect-18 ~ .focus-border:before,
.has-content.effect-18 ~ .focus-border:after{width: 50%; -webkit-transition: 0.4s; transition: 0.4s;}
.effect-18 ~ label{position: absolute; left: 0; width: 100%; top: 9px; color: #aaa; -webkit-transition: 0.3s; transition: 0.3s; z-index: 1; letter-spacing: 0.5px;}
.effect-18:focus ~ label, .has-content.effect-18 ~ label{top: -16px; font-size: 12px; color: black; -webkit-transition: 0.3s; transition: 0.3s;}
